
export default {
	onLaunch: function () {
		let token = uni.getStorageSync('token')
		let username = uni.getStorageSync('username')
		if (token && username) {
			uni.reLaunch({
				url: 'pages/index/index'
			})
		}
	},
	onShow: function () {
		console.log('App Show')
	},
	onHide: function () {
		console.log('App Hide')
	}
}
